}
.article-wrapper h1 {
  font-size: 40px;
  font-weight: bold;
  margin-bottom: 32px;
  margin-top: 32px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.article-wrapper h2 {
  font-size: 32px;
  margin-bottom: 20px;
}
