.article-wrapper ul > ol,
.article-wrapper ol > ul,
.article-wrapper ol > ol {
  margin-bottom: 0;
}
.article-wrapper ol,
.article-wrapper ul {
  padding-left: 24px;
}
.article-wrapper ol li {
  list-style: decimal;
  list-style-position: inside;
}
.article-wrapper ul {
  list-style: square;
  list-style-position: inside;
}
.article-wrapper strong {
  font-weight: bold;
}
.article-wrapper em {
  font-style: italic;
}
.article-wrapper pre {
  display: block;
  background: #ddd;
  font-family: monospace;
  padding: 12px 16px;
  overflow-x: scroll;
}
.article-wrapper p,
.article-wrapper li {
  overflow-x: hidden;
  text-overflow: ellipsis;
}
.article-wrapper img {
  max-width: 100%;
}
