.section-cd {
  border-top: 1px solid #ddd;
  position: relative;
  z-index: 1;
  background: #f7f8f8;
}
.section-cd .section-container {
  padding: 0 !important;
}
.countdown-wrapper {
  text-align: center;
  padding: 32px 16px;
  max-width: 480px;
  margin: 0 auto;
}
.countdown-wrapper .key {
  color: #333;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.countdown-wrapper .value {
  color: #999;
  margin-bottom: 24px;
}
@media screen and (min-width: 768px) {
  .section-cd .section-container {
    padding: 48px 0 !important;
  }
  .countdown-wrapper .key {
    font-size: 24px;
  }
}


